/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable @typescript-eslint/no-unused-vars */
'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useAuth } from '@/hooks/useAuth';
import {
    ArrowLeft,
    Calendar,
    MapPin,
    Clock,
    Download,
    QrCode,
    Ticket,
    Share,
    RefreshCw
} from 'lucide-react';

interface TicketInfo {
    ticketId: number;
    ticketNumber?: string;
    eventTitle?: string;
    eventDate?: string;
    eventLocation?: string;
    ticketTypeName?: string;
    attendeeFirstName?: string;
    attendeeLastName?: string;
    attendeeEmail?: string;
    qrCode?: string;
    isUsed: boolean;
    usedAt?: string;
    price?: number;
    orderNumber?: string;
    eventId: number;
    orderId?: number;
}

export default function MyTicketsPage() {
    const { user, isAuthenticated } = useAuth();
    const router = useRouter();
    const [tickets, setTickets] = useState<TicketInfo[]>([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');

    useEffect(() => {
        if (!isAuthenticated) {
            router.push('/login');
            return;
        }
        fetchMyTickets();
    }, [isAuthenticated]);

    const fetchMyTickets = async () => {
        try {
            setLoading(true);
            const token = localStorage.getItem('authToken');

            // Use the same API pattern as your events page
            const response = await fetch('http://localhost:5251/api/tickets/my-tickets', {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });

            if (response.ok) {
                const ticketsData = await response.json();
                console.log('Raw tickets data from backend:', ticketsData);

                // Check if we need to enrich tickets with event details
                const enrichedTickets = await Promise.all(
                    ticketsData.map(async (ticket: any) => {
                        console.log('Processing ticket:', ticket);

                        // Always fetch event details to get complete information
                        try {
                            const eventResponse = await fetch(`http://localhost:5251/api/events/${ticket.eventId || ticket.EventId}`);
                            if (eventResponse.ok) {
                                const eventData = await eventResponse.json();
                                console.log('Event data fetched:', eventData);

                                // Map the ticket data to our interface
                                return {
                                    ticketId: ticket.ticketId || ticket.TicketId,
                                    ticketNumber: ticket.ticketNumber || ticket.TicketNumber,
                                    eventId: ticket.eventId || ticket.EventId,
                                    eventTitle: eventData.title,
                                    eventDate: eventData.startDateTime,
                                    eventLocation: eventData.isOnline ? 'Online Event' : `${eventData.venueName}, ${eventData.venueCity}`,
                                    ticketTypeName: ticket.ticketTypeName || ticket.TicketTypeName || 'General Admission',
                                    attendeeFirstName: ticket.attendeeFirstName || ticket.AttendeeFirstName || '',
                                    attendeeLastName: ticket.attendeeLastName || ticket.AttendeeLastName || '',
                                    attendeeEmail: ticket.attendeeEmail || ticket.AttendeeEmail || '',
                                    qrCode: ticket.qrCode || ticket.QrCode,
                                    isUsed: ticket.isUsed || ticket.IsUsed || false,
                                    usedAt: ticket.usedAt || ticket.UsedAt,
                                    price: ticket.price || ticket.Price || eventData.basePrice,
                                    orderNumber: ticket.orderNumber || ticket.OrderNumber,
                                    orderId: ticket.orderId || ticket.OrderId
                                } as TicketInfo;
                            }
                        } catch (error) {
                            console.error(`Failed to fetch event details for event ${ticket.eventId || ticket.EventId}:`, error);
                        }

                        // Return ticket with fallback data if event fetch fails
                        return {
                            ticketId: ticket.ticketId || ticket.TicketId,
                            ticketNumber: ticket.ticketNumber || ticket.TicketNumber,
                            eventId: ticket.eventId || ticket.EventId,
                            eventTitle: ticket.eventTitle || ticket.EventTitle || 'Event',
                            eventDate: ticket.eventDate || ticket.EventDate,
                            eventLocation: ticket.eventLocation || ticket.EventLocation,
                            ticketTypeName: ticket.ticketTypeName || ticket.TicketTypeName || 'General Admission',
                            attendeeFirstName: ticket.attendeeFirstName || ticket.AttendeeFirstName || '',
                            attendeeLastName: ticket.attendeeLastName || ticket.AttendeeLastName || '',
                            attendeeEmail: ticket.attendeeEmail || ticket.AttendeeEmail || '',
                            qrCode: ticket.qrCode || ticket.QrCode,
                            isUsed: ticket.isUsed || ticket.IsUsed || false,
                            usedAt: ticket.usedAt || ticket.UsedAt,
                            price: ticket.price || ticket.Price,
                            orderNumber: ticket.orderNumber || ticket.OrderNumber,
                            orderId: ticket.orderId || ticket.OrderId
                        } as TicketInfo;
                    })
                );

                console.log('Final enriched tickets:', enrichedTickets);
                setTickets(enrichedTickets);
            } else {
                console.error('Failed to fetch tickets, status:', response.status);
                const errorText = await response.text();
                console.error('Error response:', errorText);
                setError('Failed to load tickets');
            }
        } catch (error) {
            console.error('Error in fetchMyTickets:', error);
            setError('Failed to load tickets');
        } finally {
            setLoading(false);
        }
    };

    const downloadTicket = async (ticketId: number) => {
        try {
            const token = localStorage.getItem('authToken');
            const response = await fetch(`http://localhost:5251/api/tickets/${ticketId}/download`, {
                headers: {
                    'Authorization': `Bearer ${token}`
                }
            });

            if (response.ok) {
                const blob = await response.blob();
                const url = window.URL.createObjectURL(blob);
                const a = document.createElement('a');
                a.href = url;
                a.download = `ticket-${ticketId}.pdf`;
                document.body.appendChild(a);
                a.click();
                window.URL.revokeObjectURL(url);
                document.body.removeChild(a);
            }
        } catch (error) {
            console.error('Failed to download ticket:', error);
        }
    };

    const shareTicket = async (ticket: TicketInfo) => {
        if (navigator.share) {
            try {
                await navigator.share({
                    title: `My ticket for ${ticket.eventTitle}`,
                    text: `I'm attending ${ticket.eventTitle}!`,
                    url: window.location.href,
                });
            } catch (error) {
                console.log('Error sharing:', error);
            }
        } else {
            navigator.clipboard.writeText(`I'm attending ${ticket.eventTitle}! Event details: ${ticket.eventDate}`);
            alert('Event details copied to clipboard!');
        }
    };

    // Use the same date formatting as your events page
    const formatDate = (dateString: string) => {
        try {
            return new Date(dateString).toLocaleDateString('en-US', {
                weekday: 'long',
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                hour: '2-digit',
                minute: '2-digit'
            });
        } catch (error) {
            return 'Invalid Date';
        }
    };

    const getTicketStatus = (ticket: TicketInfo) => {
        if (ticket.isUsed) {
            return { status: 'Used', color: 'bg-gray-100 text-gray-600', icon: '✓' };
        }

        try {
            const eventDate = new Date(ticket.eventDate || '');
            const now = new Date();

            if (isNaN(eventDate.getTime())) {
                return { status: 'Unknown', color: 'bg-yellow-100 text-yellow-600', icon: '?' };
            }

            if (eventDate < now) {
                return { status: 'Expired', color: 'bg-red-100 text-red-600', icon: '✕' };
            } else {
                return { status: 'Valid', color: 'bg-green-100 text-green-600', icon: '✓' };
            }
        } catch (error) {
            return { status: 'Unknown', color: 'bg-yellow-100 text-yellow-600', icon: '?' };
        }
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <div className="bg-white shadow-sm border-b border-gray-200">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center">
                            <button
                                onClick={() => router.back()}
                                className="flex items-center text-gray-600 hover:text-gray-900 mr-4"
                            >
                                <ArrowLeft className="h-5 w-5 mr-2" />
                                Back
                            </button>
                            <div>
                                <h1 className="text-2xl font-bold text-gray-900">My Tickets</h1>
                                <p className="text-gray-600">View and manage your event tickets</p>
                            </div>
                        </div>
                        <button
                            onClick={fetchMyTickets}
                            className="flex items-center bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700"
                        >
                            <RefreshCw className="h-4 w-4 mr-2" />
                            Refresh
                        </button>
                    </div>
                </div>
            </div>

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                {error && (
                    <div className="mb-6 bg-red-50 border border-red-200 text-red-600 px-4 py-3 rounded-md">
                        {error}
                    </div>
                )}

                {tickets.length === 0 ? (
                    <div className="text-center py-12">
                        <Ticket className="h-16 w-16 text-gray-400 mx-auto mb-4" />
                        <h3 className="text-xl font-semibold text-gray-900 mb-2">No Tickets Yet</h3>
                        <p className="text-gray-600 mb-6">You haven&apos;t purchased any tickets yet.</p>
                        <button
                            onClick={() => router.push('/events')}
                            className="bg-blue-600 text-white px-6 py-3 rounded-lg hover:bg-blue-700 font-semibold"
                        >
                            Browse Events
                        </button>
                    </div>
                ) : (
                    <div className="space-y-6">
                        {/* Summary Stats */}
                        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
                            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
                                <div className="flex items-center">
                                    <Ticket className="h-8 w-8 text-blue-600" />
                                    <div className="ml-3">
                                        <p className="text-sm font-medium text-gray-600">Total Tickets</p>
                                        <p className="text-2xl font-bold text-gray-900">{tickets.length}</p>
                                    </div>
                                </div>
                            </div>

                            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
                                <div className="flex items-center">
                                    <Calendar className="h-8 w-8 text-green-600" />
                                    <div className="ml-3">
                                        <p className="text-sm font-medium text-gray-600">Upcoming Events</p>
                                        <p className="text-2xl font-bold text-gray-900">
                                            {tickets.filter(t => {
                                                try {
                                                    const eventDate = new Date(t.eventDate || '');
                                                    return !isNaN(eventDate.getTime()) && eventDate > new Date() && !t.isUsed;
                                                } catch {
                                                    return false;
                                                }
                                            }).length}
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <div className="bg-white rounded-lg shadow-md border border-gray-200 p-6">
                                <div className="flex items-center">
                                    <QrCode className="h-8 w-8 text-purple-600" />
                                    <div className="ml-3">
                                        <p className="text-sm font-medium text-gray-600">Used Tickets</p>
                                        <p className="text-2xl font-bold text-gray-900">
                                            {tickets.filter(t => t.isUsed).length}
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>

                        {/* Tickets Grid */}
                        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                            {tickets.map((ticket) => {
                                const status = getTicketStatus(ticket);
                                return (
                                    <div key={ticket.ticketId} className="bg-white rounded-lg shadow-md border border-gray-200 overflow-hidden">
                                        {/* Ticket Header */}
                                        <div className="bg-gradient-to-r from-blue-600 to-purple-600 p-4 text-white">
                                            <div className="flex justify-between items-start">
                                                <div className="flex-1">
                                                    <h3 className="text-lg font-semibold mb-1">{ticket.eventTitle || 'Event Title'}</h3>
                                                    <p className="text-blue-100 text-sm">{ticket.ticketTypeName || 'General Admission'}</p>
                                                </div>
                                                <span className={`px-2 py-1 rounded-full text-xs font-medium ${status.color} bg-white`}>
                                                    {status.icon} {status.status}
                                                </span>
                                            </div>
                                        </div>

                                        {/* Ticket Content */}
                                        <div className="p-6">
                                            {/* Event Details */}
                                            <div className="space-y-3 mb-6">
                                                <div className="flex items-center text-gray-600">
                                                    <Calendar className="h-4 w-4 mr-3" />
                                                    <span className="text-sm">
                                                        {ticket.eventDate ? formatDate(ticket.eventDate) : 'Date TBD'}
                                                    </span>
                                                </div>

                                                <div className="flex items-center text-gray-600">
                                                    <MapPin className="h-4 w-4 mr-3" />
                                                    <span className="text-sm">
                                                        {ticket.eventLocation || 'Location TBD'}
                                                    </span>
                                                </div>

                                                <div className="flex items-center text-gray-600">
                                                    <Ticket className="h-4 w-4 mr-3" />
                                                    <span className="text-sm">
                                                        #{ticket.ticketNumber || 'TKT-' + ticket.ticketId}
                                                    </span>
                                                </div>
                                            </div>

                                            {/* Attendee Info */}
                                            <div className="bg-gray-50 rounded-lg p-4 mb-6">
                                                <h4 className="font-medium text-gray-900 mb-2">Attendee</h4>
                                                <p className="text-sm text-gray-600">
                                                    {ticket.attendeeFirstName || 'First'} {ticket.attendeeLastName || 'Last'}
                                                </p>
                                                <p className="text-sm text-gray-500">
                                                    {ticket.attendeeEmail || 'email@example.com'}
                                                </p>
                                            </div>

                                            {/* QR Code Section */}
                                            <div className="border-2 border-dashed border-gray-300 rounded-lg p-4 text-center mb-6">
                                                <QrCode className="h-16 w-16 mx-auto text-gray-400 mb-2" />
                                                <p className="text-sm text-gray-600">
                                                    {ticket.isUsed ? 'This ticket has been used' : 'Show this QR code at the event'}
                                                </p>

                                                {/* QR Code Display */}
                                                {ticket.qrCode && (
                                                    <div className="mt-3">
                                                        {(() => {
                                                            try {
                                                                // If it's JSON data, parse and show it nicely
                                                                const qrData = JSON.parse(ticket.qrCode);
                                                                return (
                                                                    <div className="bg-gray-100 p-3 rounded border text-xs font-mono space-y-1">
                                                                        <p><strong>Ticket:</strong> {qrData.TicketNumber || ticket.ticketNumber}</p>
                                                                        <p><strong>Event:</strong> {qrData.EventTitle || ticket.eventTitle}</p>
                                                                        <p><strong>Event ID:</strong> {qrData.EventId || ticket.eventId}</p>
                                                                        <p><strong>Timestamp:</strong> {qrData.Timestamp ? new Date(qrData.Timestamp).toLocaleString() : 'N/A'}</p>
                                                                        <div className="mt-2 pt-2 border-t border-gray-300">
                                                                            <p className="text-green-600"><strong>✓ Valid for Entry</strong></p>
                                                                        </div>
                                                                    </div>
                                                                );
                                                            } catch (e) {
                                                                // If it's a valid base64 image, show it
                                                                if (ticket.qrCode.match(/^[A-Za-z0-9+/]+=*$/)) {
                                                                    return (
                                                                        <img
                                                                            src={`data:image/png;base64,${ticket.qrCode}`}
                                                                            alt="QR Code"
                                                                            className="h-24 w-24 mx-auto"
                                                                            onError={(e) => {
                                                                                const target = e.target as HTMLImageElement;
                                                                                target.style.display = 'none';
                                                                                const fallback = document.createElement('div');
                                                                                fallback.className = 'bg-gray-100 p-3 rounded border text-xs';
                                                                                fallback.textContent = 'QR Code format not supported';
                                                                                target.parentNode?.appendChild(fallback);
                                                                            }}
                                                                        />
                                                                    );
                                                                } else {
                                                                    // Show raw data if neither JSON nor image
                                                                    return (
                                                                        <div className="bg-gray-100 p-3 rounded border text-xs font-mono">
                                                                            <p>QR Data: {ticket.qrCode}</p>
                                                                        </div>
                                                                    );
                                                                }
                                                            }
                                                        })()}
                                                    </div>
                                                )}

                                                {!ticket.qrCode && (
                                                    <div className="mt-3 bg-gray-100 p-3 rounded">
                                                        <p className="text-xs text-gray-500">QR Code will be generated closer to event date</p>
                                                    </div>
                                                )}
                                            </div>

                                            {/* Action Buttons */}
                                            <div className="flex space-x-3">
                                                <button
                                                    onClick={() => downloadTicket(ticket.ticketId)}
                                                    className="flex-1 bg-blue-600 text-white py-2 px-4 rounded-lg hover:bg-blue-700 transition-colors duration-200 flex items-center justify-center"
                                                >
                                                    <Download className="h-4 w-4 mr-2" />
                                                    Download
                                                </button>

                                                <button
                                                    onClick={() => shareTicket(ticket)}
                                                    className="flex-1 bg-gray-200 text-gray-700 py-2 px-4 rounded-lg hover:bg-gray-300 transition-colors duration-200 flex items-center justify-center"
                                                >
                                                    <Share className="h-4 w-4 mr-2" />
                                                    Share
                                                </button>
                                            </div>

                                            {/* Order Info */}
                                            <div className="mt-4 pt-4 border-t border-gray-200 text-xs text-gray-500 text-center">
                                                {ticket.orderNumber ? (
                                                    <>
                                                        Order #{ticket.orderNumber}
                                                        {ticket.price !== undefined && ticket.price !== null && (
                                                            <span> • ${ticket.price.toFixed(2)}</span>
                                                        )}
                                                    </>
                                                ) : (
                                                    <span>Order #N/A</span>
                                                )}
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                )}
            </div>
        </div>
    );
}